@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif; }

body {
  background-color: #202c37;
  color: white; }

a {
  text-decoration: none;
  color: inherit; }

.container {
  width: 80%;
  max-width: 1440px;
  margin: 0 auto; }

.btn {
  background: none;
  background-color: #2b3945;
  border: none;
  border-radius: 3px;
  padding: 10px 40px;
  box-shadow: 0px 0px 6px 7px rgba(0, 0, 0, 0.14);
  color: white;
  font-weight: 600;
  cursor: pointer; }
  .btn:hover {
    box-shadow: none; }

.btnDanger {
  background-color: red !important; }
  .btnDanger:hover {
    background-color: darkred !important; }

body.lightMode {
  background-color: #fafafa;
  color: #202c37; }
  body.lightMode .btn {
    background-color: white;
    color: #202c37; }
  body.lightMode .header {
    background-color: white;
    color: #202c37; }
    body.lightMode .header .modeSwitch {
      color: #202c37; }
  body.lightMode .searchBar .searchInput {
    background-color: white;
    color: #202c37; }
  body.lightMode .searchBar .custom-select__trigger {
    background-color: white;
    color: #202c37; }
  body.lightMode .searchBar .arrow::before,
  body.lightMode .searchBar .arrow::after {
    background-color: #202c37; }
  body.lightMode .searchBar .custom-options {
    background-color: white; }
    body.lightMode .searchBar .custom-options .custom-option {
      color: #202c37; }
      body.lightMode .searchBar .custom-options .custom-option:hover {
        background-color: #fafafa; }
  body.lightMode .countryCard {
    background-color: white; }

.countryCard {
  border-radius: 5px;
  overflow: hidden;
  background-color: #2b3945;
  box-shadow: 0px 0px 6px 7px rgba(0, 0, 0, 0.14); }
  .countryCard:hover {
    box-shadow: none;
    transition: 0.3s ease-in; }
  .countryCard .flag {
    width: 100%;
    height: 150px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat; }
  .countryCard .details {
    padding: 20px 20px 40px; }
    .countryCard .details h2 {
      font-size: 1.2rem;
      margin-bottom: 20px; }
    .countryCard .details h3 {
      font-size: 1rem;
      margin-bottom: 0.2rem; }
      .countryCard .details h3 span {
        font-weight: 300; }

.header {
  background-color: #2b3945;
  box-shadow: 0px 0px 6px 7px rgba(0, 0, 0, 0.14);
  padding: 30px 0;
  margin-bottom: 70px; }
  .header .container {
    display: flex;
    justify-content: space-between; }
    .header .container button {
      background: none;
      border: none;
      color: white;
      font-size: 1.1rem;
      font-weight: 600;
      cursor: pointer; }
      .header .container button:hover {
        color: grey;
        transition: color 0.2s ease-in; }

.searchBar {
  margin-bottom: 50px; }
  .searchBar .container {
    display: flex;
    justify-content: space-between; }
  .searchBar form {
    width: 50%;
    position: relative;
    display: flex; }
    .searchBar form label {
      position: absolute;
      top: 16px;
      left: 24px;
      font-size: 1.5rem; }
    .searchBar form .searchInput {
      height: 60px;
      width: 60%;
      padding-left: 80px;
      background-color: #2b3945;
      border: none;
      color: white;
      font-size: 1rem;
      font-weight: 600;
      border-radius: 5px;
      box-shadow: 0px 0px 6px 7px rgba(0, 0, 0, 0.14); }
    .searchBar form button {
      margin-left: 10px; }
  .searchBar .regionPicker {
    background: #2b3945;
    border: none;
    padding: 20px;
    color: white;
    font-weight: 600; }

.custom-select-wrapper {
  position: relative;
  width: 30%; }

.custom-select {
  position: relative;
  display: flex;
  flex-direction: column;
  background-color: #2b3945;
  border-radius: 6px;
  box-shadow: 0px 0px 6px 7px rgba(0, 0, 0, 0.14); }

.custom-select__trigger {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 22px;
  font-size: 20px;
  font-weight: 300;
  color: white;
  height: 60px;
  line-height: 60px;
  cursor: pointer; }

.custom-options {
  position: absolute;
  display: block;
  top: 120%;
  left: 0;
  right: 0;
  border-radius: 6px;
  box-shadow: 0px 0px 6px 7px rgba(0, 0, 0, 0.14);
  background: #2b3945;
  transition: all 0.5s;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 2; }

.custom-select.open .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all; }

.custom-option {
  position: relative;
  display: block;
  padding: 0 22px 0 22px;
  font-size: 22px;
  font-weight: 300;
  color: white;
  line-height: 60px;
  cursor: pointer;
  transition: all 0.5s; }

.custom-option:hover {
  cursor: pointer;
  background-color: #202c37; }

.custom-option.selected {
  color: #ffffff;
  background-color: #305c91; }

.arrow {
  position: relative;
  height: 15px;
  width: 15px; }

.arrow::before,
.arrow::after {
  content: '';
  position: absolute;
  bottom: 0px;
  width: 0.15rem;
  height: 100%;
  transition: all 0.5s; }

.arrow::before {
  left: -5px;
  transform: rotate(45deg);
  background-color: white; }

.arrow::after {
  left: 5px;
  transform: rotate(-45deg);
  background-color: white; }

.open .arrow::before {
  left: -5px;
  transform: rotate(-45deg); }

.open .arrow::after {
  left: 5px;
  transform: rotate(45deg); }

.countryGrid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-row-gap: 3vw;
  row-gap: 3vw;
  grid-column-gap: 3vw;
  -webkit-column-gap: 3vw;
          column-gap: 3vw; }

@media only screen and (max-width: 1150px) {
  .countryGrid {
    grid-template-columns: repeat(3, 1fr); }
  .custom-select-wrapper {
    width: 40%; }
  .searchBar form {
    width: 60%; }
    .searchBar form .searchInput {
      width: 70%; }
  .countryPage .mainContent {
    grid-template-columns: 1fr !important; }
    .countryPage .mainContent img {
      margin-bottom: 30px; } }

@media only screen and (max-width: 850px) {
  .countryGrid {
    grid-template-columns: repeat(2, 1fr); }
  .custom-select-wrapper {
    width: 100%; }
  .searchBar .container {
    display: block; }
    .searchBar .container form {
      width: 100%;
      margin-bottom: 30px; }
      .searchBar .container form .searchInput {
        width: 80%; }
  .borderCountriesButtons {
    flex-direction: column; }
    .borderCountriesButtons h4 {
      margin: 0;
      margin-bottom: 20px;
      width: 100%;
      text-align: center; }
    .borderCountriesButtons .buttonHolder {
      width: 100%;
      align-items: center;
      justify-content: center; } }

@media only screen and (max-width: 550px) {
  .countryGrid {
    grid-template-columns: 1fr; }
  .searchBar .container form {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 30px; }
    .searchBar .container form button {
      margin: 0;
      height: 60px; }
    .searchBar .container form .searchBtn {
      margin-bottom: 30px; }
    .searchBar .container form .searchInput {
      margin-bottom: 30px;
      width: 100%; }
  .countryPage .mainContent h1 {
    text-align: center; }
  .countryPage .mainContent .countryBody {
    grid-template-columns: 1fr !important;
    text-align: center; }
  .modal .modalBox {
    padding: 10px !important; } }

.countryPage .btnBack {
  margin-bottom: 50px; }

.countryPage .mainContent {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  grid-column-gap: 5%;
  -webkit-column-gap: 5%;
          column-gap: 5%; }
  .countryPage .mainContent .col {
    min-width: 50%; }
  .countryPage .mainContent .flag {
    width: 90%;
    height: 90%; }
  .countryPage .mainContent img {
    box-shadow: 0px 0px 6px 7px rgba(0, 0, 0, 0.14);
    width: 100%;
    min-width: 30vw; }
  .countryPage .mainContent .countryDetails h1 {
    margin-bottom: 30px; }
  .countryPage .mainContent .countryBody {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 50px; }
    .countryPage .mainContent .countryBody h4 {
      margin-bottom: 10px; }
      .countryPage .mainContent .countryBody h4 span {
        font-weight: 300; }
  .countryPage .mainContent .borderCountriesButtons {
    display: flex;
    align-items: center; }
    .countryPage .mainContent .borderCountriesButtons .buttonHolder {
      display: flex;
      flex-wrap: wrap; }
    .countryPage .mainContent .borderCountriesButtons h4 {
      margin-right: 15px;
      width: 175px; }
    .countryPage .mainContent .borderCountriesButtons a.btn {
      margin-bottom: 7px;
      margin-right: 7px;
      flex-grow: 1;
      text-align: center; }

.modal {
  position: absolute;
  width: 100vw;
  height: 100vh;
  display: grid;
  justify-content: center;
  align-items: flex-start;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 10;
  top: 0;
  position: absolute; }
  .modal .modalBox {
    border: solid 4px red;
    background-color: darkred;
    border-radius: 5px;
    margin: 100px 15px 0;
    padding: 100px;
    display: flex;
    flex-direction: column;
    color: white; }
  .modal h4 {
    text-align: center;
    margin: 10px; }
  .modal .btn {
    margin: 10px;
    align-self: flex-end; }

